.solar-syst div {
    border-radius: 1000px;
    top: 50%;
    left: 50%;
    position: absolute;
    z-index: 999;
}
.solar-syst div:not(.sun) {
    border: 1px solid rgba(102, 166, 229, 0.12);
}
.solar-syst div:not(.sun):before {
    left: 50%;
    border-radius: 100px;
    content: '';
    position: absolute;
}
.solar-syst div:not(.asteroids-belt):before {
    box-shadow: inset 0 6px 0 -2px rgba(0, 0, 0, 0.25);
}

.sun {
    background: radial-gradient(ellipse at center, #ffd000 1%, #f9b700 39%, #f9b700 39%, #e06317 100%);
    height: 40px;
    width: 40px;
    margin-top: -20px;
    margin-left: -20px;
    background-clip: padding-box;
    border: 0 !important;
    background-position: -28px -103px;
    background-size: 175%;
    box-shadow: 0 0 10px 2px rgba(255, 107, 0, 0.4), 0 0 22px 11px rgba(255, 203, 0, 0.13);
}

.mercury {
    height: 70px;
    width: 70px;
    margin-top: -35px;
    margin-left: -35px;
    -webkit-animation: orb 7.1867343561s linear infinite;
    animation: orb 7.1867343561s linear infinite;
}
.mercury:before {
    height: 4px;
    width: 4px;
    background: #9f5e26;
    margin-top: -2px;
    margin-left: -2px;
}

.venus {
    height: 100px;
    width: 100px;
    margin-top: -50px;
    margin-left: -50px;
    -webkit-animation: orb 18.4555338265s linear infinite;
    animation: orb 18.4555338265s linear infinite;
}
.venus:before {
    height: 8px;
    width: 8px;
    background: #beb768;
    margin-top: -4px;
    margin-left: -4px;
}

.earth {
    height: 145px;
    width: 145px;
    margin-top: -72.5px;
    margin-left: -72.5px;
    -webkit-animation: orb 30s linear infinite;
    animation: orb 30s linear infinite;
}
.earth:before {
    height: 6px;
    width: 6px;
    background: #11abe9;
    margin-top: -3px;
    margin-left: -3px;
}
.earth:after {
    position: absolute;
    content: '';
    height: 18px;
    width: 18px;
    left: 50%;
    top: 0px;
    margin-left: -9px;
    margin-top: -9px;
    border-radius: 100px;
    box-shadow: 0 -10px 0 -8px grey;
    -webkit-animation: orb 2.2440352158s linear infinite;
    animation: orb 2.2440352158s linear infinite;
}

.mars {
    height: 190px;
    width: 190px;
    margin-top: -95px;
    margin-left: -95px;
    -webkit-animation: orb 56.4261314589s linear infinite;
    animation: orb 56.4261314589s linear infinite;
}
.mars:before {
    height: 6px;
    width: 6px;
    background: #cf3921;
    margin-top: -3px;
    margin-left: -3px;
}

.jupiter {
    height: 340px;
    width: 340px;
    margin-top: -170px;
    margin-left: -170px;
    -webkit-animation: orb 355.7228171013s linear infinite;
    animation: orb 355.7228171013s linear infinite;
}
.jupiter:before {
    height: 18px;
    width: 18px;
    background: #c76e2a;
    margin-top: -9px;
    margin-left: -9px;
}

.saturn {
    height: 440px;
    width: 440px;
    margin-top: -220px;
    margin-left: -220px;
    -webkit-animation: orb 882.6952471456s linear infinite;
    animation: orb 882.6952471456s linear infinite;
}
.saturn:before {
    height: 12px;
    width: 12px;
    background: #e7c194;
    margin-top: -6px;
    margin-left: -6px;
}
.saturn:after {
    position: absolute;
    content: '';
    height: 2.34%;
    width: 4.676%;
    left: 50%;
    top: 0px;
    -webkit-transform: rotateZ(-52deg);
    transform: rotateZ(-52deg);
    margin-left: -2.3%;
    margin-top: -1.2%;
    border-radius: 50% 50% 50% 50%;
    box-shadow: 0 1px 0 1px #987641, 3px 1px 0 #987641, -3px 1px 0 #987641;
    -webkit-animation: orb 882.6952471456s linear infinite;
    animation: orb 882.6952471456s linear infinite;
    animation-direction: reverse;
    -webkit-transform-origin: 52% 60%;
